import { useCallback, useMemo } from "react";
export function useStateUpdate(setState) {
    return useMemo(() => ({
        set: setState,
        update: setState
    }), [setState]);
}
export function useMappedSetter(key, set) {
    return useCallback((x) => {
        set((oldT) => {
            let newT = { ...oldT };
            if (typeof x === "function") {
                let u = x;
                newT[key] = u(oldT[key]);
            }
            else {
                newT[key] = x;
            }
            return newT;
        });
    }, [set, key]);
}
export function useSetter(value, set) {
    return useCallback((x) => {
        if (typeof x === "function") {
            let u = x;
            set(u(value));
        }
        else {
            set(x);
        }
    }, [value, set]);
}
